@import '../../style-utility/variables';

.robot{
    width: 100vw;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    flex: 0.5;

    .antenna{
        width: 30px;
        height: 30px;
        background: linear-gradient(to bottom right, #c79c9c, #aa7070) ;
        border-radius: 50%;
        border: 5px solid black;
        animation: rotating 4s;
        -webkit-animation: rotating 4s linear infinite;

        .white-point{
            width: 30%;
            height: 30%;
            margin-top: 50%;
            transform: translate(200%, 20%);
            border-radius: 50%;
            background-color: rgb(224, 221, 221);
        }
    }

    @keyframes rotating {
        from {
            transform: rotate(0deg);
        }
        to{
            transform: rotate(360deg);
        }
    }


    .vertical-line{
        width: 5px;
        height: 10px;
        background-color: black;
    }

    .head-square{
        width: 120px;
        height: 120px;
        background-color: black;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 5%;

        .face-square{
            width: 90%;
            height: 90%;
            background-color: #e4bf94;

            .face-container{
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                background:  linear-gradient(to bottom right, #e4bf94, rgb(214, 170, 170));

            .top{
                flex: 0.5;
                display: flex;
                justify-content: space-around;

                span{
                    margin-top: 20%;
                    width: 20%;
                    height: 30%;
                    border-radius: 25%;
                    background-color: white;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: flex-end;

                    span{
                        width: 30%;
                        height: 40%;
                        background-color: black;
                    }
                }
            }   
    
            .bottom{
                flex: 0.5;
                display: flex;
                align-items: center;
                justify-content: center;

                .mouth{
                    width: 60%;
                    height: 5%;
                    background-color: black;
                }
            }
        }
    }
    }

    .body-rect{
        width: 120px;
        height: 200px;
        background-color: black;
        border-radius: 5%;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow-x: visible;

        .inner-body-rect{
            width: 90%;
            height: 95%;
            background-color: #e4bf94;
            overflow-x: visible;

            .inner-body-container{
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: space-between;
                background:  linear-gradient(to bottom right, #e4bf94, rgb(214, 170, 170));
                overflow-x: visible;
            
                .left-arm{
                    width: 5%;
                    height: 50%;
                    background-color: black;
                    margin-top: 10%;
                    transform: rotate(30deg) translate(-200%, 40%);
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-end;
                    overflow-x: visible;

                .left-hand{
                    width: 20px;
                    height: 20px;
                    transform: rotate(30deg) translate(20%, 90%);
                    background-color: black;
                    border-radius: 30%;
                    display: flex;
                    justify-content: flex-end;
                    overflow-x: visible;

                        .left-hand-space{
                            width: 15px;
                            height: 17px;
                            background: linear-gradient(to bottom, #23172d, #24182e);
                            border-radius: 50%;
                            transform: translate(10%, 31%);    
                            overflow-x: visible;             
                        }
                    }
                }

                .right-arm{
                    width: 5%;
                    height: 50%;
                    background-color: black;
                    margin-top: 10%;
                    transform: rotate(-30deg) translate(200%, 40%);
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-end;
                    overflow-x: visible;

                    .right-hand{
                        width: 20px;
                        height: 20px;
                        transform: rotate(30deg) translate(30%, 100%);
                        background-color: black;
                        border-radius: 30%;
                        display: flex;
                        flex-direction: column;
                        justify-content: flex-end;
                        overflow-x: visible;

                        .right-hand-space{
                            width: 15px;
                            height: 17px;
                            background: linear-gradient(to bottom, #23172d, #24182e);
                            border-radius: 50%;
                            transform: translate(40%, 13%);
                            overflow-x: visible;
                        }
                    }
                }
            }
        }
    }
    .legs{
        width: 120px;
        height: 150px;
        display: flex;
        justify-content: space-around;
        overflow-x: visible;

        .left{
            background-color: black;
            width: 10%;
            height: 80%;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            overflow-x: visible;
        }
        .right{
            background-color: black;
            width: 10%;
            height: 80%;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            overflow-x: visible;
        }
        .foot{
            width: 50px;
            height: 10px;
            background-color: black;
            overflow-x: visible;
        }
    }
}

@media (max-width: $tablet-view-small) {
  .robot{
    transform: translate(0, 3%);

    .antenna {
      width: 15px;
      height: 17px;
      background: linear-gradient(to bottom right, #c79c9c, #aa7070);
      border-radius: 50%;
      border: 3px solid black;
      animation: rotating 4s;
      -webkit-animation: rotating 4s linear infinite;
    }

    .head-square {
      width: 60px;
      height: 60px;
      background-color: black;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 5%;
    }

    .body-rect{
      width: 60px;
      height: 100px;
      background-color: black;
      border-radius: 5%;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow-x: visible;
      
      .inner-body-rect{
        .inner-body-container{
        .left-arm{
          .left-hand{
            .left-hand-space{
              background: linear-gradient(to bottom, #2b2228, #2b2227);
              }
            }
          }
        .right-arm{
          .right-hand{
            .right-hand-space{
              background: linear-gradient(to bottom, #2a2128, #2b2128);
              }
            }
          }
        }
      }
    }

    .legs{
      width: 50px;
      height: 80px;
      display: flex;
      justify-content: space-around;
      overflow-x: visible;

      .foot {
        width: 15px;
        height: 10px;
        background-color: black;
        overflow-x: visible;
      }
    }
  }
}